.container {
	position: absolute;
	width: max-content;
	padding: 5px 10px;
	background-color: var(--dark-opac-black);
	color: #fff;
	border-radius: 4px;
	font-family: var(--font-poppins);
	font-size: 1rem;
	cursor: default;
}

.txt::after {
	content: '';
	position: absolute;
	top: 50%;
	right: 100%;
	margin-top: -5px;
	border-width: 5px;
	border-style: solid;
	border-color: transparent black transparent transparent
}