.container {
	height: 400px;
	background-color:var(--opac-black);
	padding-top: 40px;
	padding-bottom: 40px;
}

.contactHeading {
	font-family: var(--font-montserrat);
	font-size: 1.5rem;
	font-weight: 700;
}

.linkContainer {
	display: flex;
	flex-direction: column;
	width: max-content;
	margin-top: 20px;
}

.link {
	margin: 10px 0;
	width: fit-content;
	font-family: var(--font-poppins);
}

.rights {
	text-align: center;
	font-family: var(--font-montserrat);
	font-weight: 700;
	position: relative;
	top: 25px;
}