.circleLoader {
    border: 4px solid rgba(0, 0, 0, 0.2);
    border-left-color: #4bc44b;
    animation: loader-spin 1.2s infinite linear;
    position: relative;
    display: inline-block;
    vertical-align: top;
    border-radius: 50%;
    margin: 0 5px;
    min-width: 40px;
	height: 40px;
}

.loadComplete {
    -webkit-animation: none;
    animation: none;
    border-color: #4bc44b;
    transition: border 500ms ease-out;
}

.checkmark.draw::after {
    animation-duration: 800ms;
    animation-timing-function: ease;
    animation-name: checkmark;
    transform: scaleX(-1) rotate(135deg);
}

.checkmark::after {
    opacity: 1;
    height: 20px;
    width: 8px;
    transform-origin: left top;
    border-right: 4px solid #4bc44b;
    border-top: 4px solid #4bc44b;
    content: '';
    left: 3px;
    top: 18px;
    position: absolute;
}

.error {
	-webkit-animation: none;
    animation: none;
    border-color: #4bc44b;
    transition: 1s all ease-out;
}

.wrong.draw::after {
	animation-duration: 1s;
    animation-timing-function: ease;
	animation-name: wrongmarkright;
	transform: rotate(45deg);
	height: 22px;
}

.wrong.draw::before {
	animation-duration: 1s;
    animation-timing-function: ease;
	animation-name: wrongmarkleft;
	transform: rotate(-45deg);
	height: 22px;
}

.wrong::before {
	opacity: 1;
    height: 22px;
    width: 0;
    border-right: 4px solid #4bc44b;
    border-top: 4px solid #4bc44b;
    content: '';
    left: 14px;
    top: 3px;
    position: absolute;
}

.wrong::after {
	opacity: 1;
    height: 22px;
    width: 0;
    border-right: 4px solid #4bc44b;
    border-top: 4px solid #4bc44b;
    content: '';
    left: 14px;
    top: 3px;
    position: absolute;
}

@keyframes loader-spin {
    0% {
        transform: rotate(0deg);
    }

    100% {
        transform: rotate(360deg);
    }
}

@keyframes checkmark {
    0% {
        height: 0;
        width: 0;
        opacity: 1;
    }

    20% {
        height: 0;
        width: 6px;
        opacity: 1;
    }

    40% {
        height: 15px;
        width: 6px;
        opacity: 1;
    }

    100% {
        height: 20px;
        width: 8px;
        opacity: 1;
    }
}

@keyframes wrongmarkright {
	0% {
		height: 11px;
	}
	25% {
		height: 22px;
	}
	50% {
		transform: rotate(22.5deg);
		height: 22px;
	}
	100% {
		transform: rotate(45deg);
		height: 22px;
	}
}

@keyframes wrongmarkleft {
	0% {
		height: 11px;
	}
	25% {
		height: 22px;
	}
	50% {
		transform: rotate(-22.5deg);
		height: 22px;
	}
	100% {
		transform: rotate(-45deg);
		height: 22px;
	}
}