.container {
	width: 100%;
	margin: 80px 0;
}

.heading {
	font-family: var(--font-montserrat);
	font-size: 2rem;
}

.projectsContainer {
	display: grid;
	grid-template-columns: repeat(4, auto);
	justify-content: space-between;
	gap: 20px;
	width: 100%;
	margin-top: 40px;
}

.projects {
	width: 100%;
	height: 100%;
	max-width: 350px;
	max-height: 215px;
	position: relative;
	cursor: pointer;
	overflow: hidden;
	border: 3px solid var(--primary-blue);
	border-radius: 6px;
	box-shadow: 3px 2px 10px 1px var(--opac-black);
	transition: 0.2s all ease-in-out;
}

.projects div {
	width: 100%;
	height: 100%;
}

.projects:hover {
	transform: scale(1.02);
	box-shadow: 8px 5px 10px 2px var(--opac-black);
}

.projects img {
	width: 100%;
	height: 100%;
}

.projectInfo {
	position: absolute;
	display: flex;
	justify-content: center;
	align-items: center;
	height: 100%;
	width: 100%;
	font-family: var(--font-poppins);
	font-size: 1.3rem;
	text-align: center;
	color: #fff;
	background-color: var(--opac-black);
	transition: 0.2s all ease-in-out;
}

.projects:hover .projectInfo {
	transform: translateY(-100%);
}

@media screen and (max-width: 1300px) {
	.projectsContainer {
		grid-template-columns: repeat(3, auto);
	}
}

@media screen and (max-width: 875px) {
	.projectsContainer {
		grid-template-columns: repeat(2, auto);
	}

	.projectInfo {
		transform: translateY(-100%);
	}
}

@media screen and (max-width: 500px) {
	.projectsContainer {
		grid-template-columns: repeat(1, auto);
		justify-content: center;
	}
}