.container {
	width: 40%;
}

.linksContainer {
	width: 100%;
	display: flex;
	flex-direction: 'row';
	justify-content: space-between;
	align-items: center;
	list-style: none;
}

.links {
	font-size: 1.1rem;
	padding: 5px 10px;
	color: #fff;
	font-family: var(--font-poppins);
	font-weight: 700;
	letter-spacing: 1px;
	position: relative;
}

.links.highlight {
	background-color: var(--primary-blue);
	border-radius: 4px;
}

.links::after {
	width: 0;
}

.links:hover::after {
	width: 100%;
}

.links.active::after {
	width: 100%;
}

@media screen and (max-width: 1375px) {
	.container {
		width: 50%;
	}
}

@media screen and (max-width: 1100px) {
	.container {
		width: 58%;
	}
}

/* Mobile View */

@media screen and (max-width: 875px) {
	.container {
		position: absolute;
		top: 80px;
		left: 0;
		width: 100%;
		height: 0;
		overflow: hidden;
		background-color: var(--dark-opac-black);
		padding: 0 6vw;
		transition: 0.2s height ease-in-out;
	}

	.navOpen {
		height: 40vh;
		overflow: auto !important;
	}

	.linksContainer {
		height: 100%;
		flex-direction: column;
		justify-content: space-around;
	}

	.linkContainer {
		width: 100%;
	}

	.links {
		display: block;
		width: 100%;
		padding: 10px;
		text-align: center;
	}
}

@media screen and (max-width: 600px) {
	.container {
		padding: 0 3vw;
	}
}