.wrapper {
	height: calc(100% - 80px);
	position: relative;
	top: 80px;
	overflow-y: hidden;
}

.introOpen {
	transform: translateY(0) !important;
}

.container {
	display: flex;
	justify-content: space-between;
	align-items: center;
	width: 100%;
	height: 100%;
	background-color: var(--light-opac-black);
	transform: translateY(100%);
	transition: 0.5s transform ease-in-out;
}

.txtContainer {
	transform: translateY(-10%);
}

.txt {
	color: #fff;
	font-family: var(--font-montserrat);
}

.initialWelcTxt {
	font-size: 6rem !important;
}

.welcTxt {
	font-size: 2rem;
	margin-bottom: 10px;
	transition: 0.5s all ease-in-out;
}

.introTxt {
	max-width: 930px;
	min-height: 100px;
	font-size: 6rem;
	line-height: 130%;
}

.cursor::after {
	content: '';
	display: inline-block;
	position: absolute;
	bottom: 20px;
	width: 2px;
	height: 90px;
	background-color: rgb(180, 180, 180);
}

.line1::after, .line2::after {
	height: 5px;
	border-radius: 0;
	transition: 0.2s width ease-in-out !important;
}

.line1::after {
	width: 30%;
	transform: translateY(10px);
}

.line2::after {
	width: 15%;
	transform: translateY(45px);
}

@media screen and (max-width: 1134px) {
	.txtContainer {
		max-width: 630px;
	}
}

@media screen and (max-width: 560px) {
	.txtContainer {
		max-width: 500px;
	}

	.initialWelcTxt {
		font-size: 2rem !important;
	}

	.welcTxt {
		font-size: 1.5rem;
	}
	
	.introTxt {
		font-size: 4rem;
		min-height: 70px;
	}

	.cursor::after {
		height: 70px;
		bottom: 10px;
	}
}

@media screen and (max-width: 370px) {
	.introTxt {
		font-size: 3.5rem;
	}

	.cursor::after {
		height: 55px;
	}
}