.container {
	margin-top: 80px;
	padding-top: 30px;
	padding-bottom: 30px;
	background-color: var(--primary-red);
}

.heading {
	font-size: 2rem;
	font-family: var(--font-montserrat);
}

.content {
	display: flex;
	flex-direction: row;
	align-items: center;
	justify-content: space-between;
	padding: 40px 0;
}

.image {
	width: 250px;
	height: 250px;
}

.text {
	margin-left: 30px;
	font-size: 1.3rem;
	font-family: var(--font-poppins);
	text-align: justify;
	max-width: 900px;
}

@media screen and (max-width: 810px) {
	.content {
		flex-direction: column;
	}

	.text {
		margin-left: 0;
		margin-top: 30px;
	}
}