.container {
	display: flex;
	flex-direction: column;
	justify-content: space-around;
	align-items: center;
	height: 350px;
	background-color: var(--primary-red);
}

.subscribeTxt {
	font-family: var(--font-montserrat);
	font-size: 2rem;
	text-align: center;
}

.inputContainer {
	text-align: center;
	width: 100%;
}

.emailInput {
	width: 100%;
	max-width: 540px;
	font-family: var(--font-poppins);
}

.messageContainer {
	height: 40px;
	margin: 5px 0;
	position: relative;
}

.errMessage {
	font-size: 15px;
	top: 3px;
	left: calc(50% + 35px);
}

.submitBtn {
	font-weight: 700;
	text-transform: uppercase;
	transition: 0.2s all ease-in-out;
}

.submitBtn:hover {
	transform: scale(1.05);
}

@media screen and (max-width: 430px) {
	.spinner {
		left: -80px;
	}

	.errMessage {
		left: 38%;
	}
}