.wrapper {
	margin-top: 80px;
}

.container {
	border-top: 2px solid #000;
	border-bottom: 2px solid #000;
	height: 200px;
	margin-top: 30px;
	margin-bottom: 30px;
}

.heading {
	font-size: 2rem;
	font-family: var(--font-montserrat);
}

.description {
	margin-top: 20px;
	font-family: var(--font-poppins);
}

.description p {
	margin-top: 10px;
}

.coffeeImg {
	height: 40px;
	position: relative;
	top: 10px;
}

.linksContainer {
	display: flex;
	height: 100%;
	justify-content: space-evenly;
	align-items: center;
	padding: 20px 0;
}

.linkWrapper {
	text-align: center;
	height: 100%;
	position: relative;
}

.linkName {
	font-family: var(--font-poppins);
}

.link {
	filter: grayscale(90%);
	transform: translateY(calc(50% + 24px));
	transition: 0.1s all ease-in-out;
}

.link:hover {
	filter: none;
}

@media screen and (max-width: 875px) {
	.link {
		filter: none;
	}
}

@media screen and (max-width: 650px) {
	.linksContainer {
		justify-content: space-between;
	}
}

@media screen and (max-width: 350px) {
	.link {
		transform: translateY(200%);
	}

	.linkName {
		position: absolute;
		top: 40%;
		transform-origin: left;
		transform: rotate(-90deg);
	}
}