.container {
	max-width: 470px;
	padding: 20px;
	margin: 0 auto;
	border: 3px solid var(--primary-blue);
	border-radius: 6px;
}

.heading {
	font-size: 2rem;
	font-family: var(--font-montserrat);
	color: var(--primary-red);
	margin-bottom: 20px;
}

.message {
	font-family: var(--font-poppins);
	font-size: 1rem;
}

.retryBtn {
	font-family: var(--font-poppins);
	font-weight: 700;
	margin-top: 20px;
	position: relative;
	left: calc(100% - 75px);
}