.btn {
	padding: 12px 16px;
	background-color: var(--primary-blue);
	border: none;
	border-radius: 6px;
	cursor: pointer;
	font-size: 1rem;
	color: #fff;
	user-select: none;
}

.inp {
	border: 3px solid var(--primary-blue);
	outline: none;
	font-size: 1.3rem;
	padding: 8px 12px;
	border-radius: 6px;
}

.inp:invalid {
	box-shadow: none;
}

.link {
	color: var(--primary-blue);
	position: relative;
}

.link::after {
	content: '';
	display: inline-block;
	position: absolute;
	top: 100%;
	left: 0;
	padding: 0 1px;
	height: 1px;
	width: 100%;
	background-color: var(--primary-blue);
	transition: 0.2s width, padding ease-in-out;
}

.link:hover::after {
	width: 0;
	padding: 0;
}