:root {
	--white: #fff;
	--primary-red: #D34545;
	--primary-blue: #61C4FB;
	--opac-black: #00000090;
	--light-opac-black: #00000070;
	--dark-opac-black: #000000e0;
	--font-montserrat: 'Montserrat', sans-serif;
    --font-poppins: 'Poppins', sans-serif;
}

::selection {
	background-color: var(--primary-blue);
	color: #fff;
}

* {
	margin: 0;
	padding: 0;
	box-sizing: border-box;
}

html {
	scroll-behavior: smooth;
}

a {
	text-decoration: none;
	outline: none;
}

.section {
	padding: 0 3vw;
}

@media screen and (min-width: 600px) {
	.section {
		padding: 0 6vw;
		max-width: 1920px;
		margin: auto;
	}
}

@media screen and (min-width: 900px) {
	.section {
		padding: 0 9vw
	}
}

@media screen and (max-width: 330px) {
	.section {
		padding: 0 5px;
	}
}