.container {
	width: 100%;
	height: 80px;
	display: flex;
	justify-content: space-between;
	align-items: center;
	border-bottom: 2px solid var(--primary-blue);
	background-color: var(--dark-opac-black);
	position: fixed;
	top: 0;
	z-index: 2;
}

@media screen and (min-width: 876px) {
	.hideBurger {
		display: none;
	}
}